
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
}

.home{
  position: absolute;
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, .4)), to(rgba(0, 0, 0, 1)));
  background: linear-gradient(rgba(0, 0, 0, .4), rgba(0, 0, 0, 1));
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.content-box{
  display: inline-block;
  position: relative;
  height: 100%;
}
.bg-img{
  width: auto;
  height: 100%;
}
.left-door, .right-door{
  position: absolute;
  bottom: 0;
}
.left-door{
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-transform: rotateY(-50deg);
          transform: rotateY(-50deg);
}
.left-door-ani{
  -webkit-animation: left-door-close 2s ease-out forwards;
          animation: left-door-close 2s ease-out forwards;
}
.right-door{
  -webkit-transform-origin: right;
          transform-origin: right;
  -webkit-transform: rotateY(50deg);
          transform: rotateY(50deg);
}
.right-door-ani{
  -webkit-animation: right-door-close 2s ease-out forwards;
          animation: right-door-close 2s ease-out forwards;
}

/* 纸的动画 */
.paper-pos{
  position: absolute;
  overflow: hidden;
  /* 卷纸动画500ms */
  -webkit-transition: height 500ms linear;
  transition: height 500ms linear;
  z-index: 11;
}
.drop-paper{
  -webkit-animation: paper-fly-in-pill 1.2s ease-in-out forwards;
          animation: paper-fly-in-pill 1.2s ease-in-out forwards;
}

/* 胶囊动画及样式 */
.pill-bottom, .pill-top{
  position: absolute;
  bottom: 0;
  z-index: 12;
}
.pill-top{
  -webkit-transform: rotateZ(-120deg);
          transform: rotateZ(-120deg);
  -webkit-transition: left 600ms ease-in, bottom 600ms linear;
  transition: left 600ms ease-in, bottom 600ms linear;
}
.pill-top-rotate {
  -webkit-animation: rotate-pill-top 600ms ease-in forwards;
          animation: rotate-pill-top 600ms ease-in forwards;
}
.pill-whole{
  position: absolute;
  -webkit-transform-origin: center;
          transform-origin: center;
  -webkit-transition: left 1s ease-out, bottom 1s ease-out;
  transition: left 1s ease-out, bottom 1s ease-out;
  -webkit-animation: rotate-pill 1s ease-in forwards;
          animation: rotate-pill 1s ease-in forwards;
}

/* 关门动画 */
@-webkit-keyframes left-door-close {
0%{
    -webkit-transform: rotateY(-50deg);
            transform: rotateY(-50deg);
}
100%{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
}
@keyframes left-door-close {
0%{
    -webkit-transform: rotateY(-50deg);
            transform: rotateY(-50deg);
}
100%{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
}
@-webkit-keyframes right-door-close {
0%{
    -webkit-transform: rotateY(50deg);
            transform: rotateY(50deg);
}
100%{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
}
@keyframes right-door-close {
0%{
    -webkit-transform: rotateY(50deg);
            transform: rotateY(50deg);
}
100%{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
}

/* 卷纸动画 */
@-webkit-keyframes paper-fly-in-pill {
0%{
    -webkit-transform: rotateZ(90deg) scale(.8);
            transform: rotateZ(90deg) scale(.8);
}
100%{
    -webkit-transform: rotateZ(90deg) scale(.3);
            transform: rotateZ(90deg) scale(.3);
    bottom: 30px;
}
}
@keyframes paper-fly-in-pill {
0%{
    -webkit-transform: rotateZ(90deg) scale(.8);
            transform: rotateZ(90deg) scale(.8);
}
100%{
    -webkit-transform: rotateZ(90deg) scale(.3);
            transform: rotateZ(90deg) scale(.3);
    bottom: 30px;
}
}

/* 旋转胶囊-上方 */
@-webkit-keyframes rotate-pill-top {
0%{
    -webkit-transform: rotateZ(-120deg);
            transform: rotateZ(-120deg);
}
100%{
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
}
@keyframes rotate-pill-top {
0%{
    -webkit-transform: rotateZ(-120deg);
            transform: rotateZ(-120deg);
}
100%{
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg);
}
}

/* 旋转胶囊 */
@-webkit-keyframes rotate-pill {
0%{
    -webkit-transform: rotateZ(0deg) scale(.8);
            transform: rotateZ(0deg) scale(.8);
}
25%{
    -webkit-transform: rotateZ(180deg) scale(.7);
            transform: rotateZ(180deg) scale(.7);
}
50%{
    -webkit-transform: rotateZ(360deg) scale(.6);
            transform: rotateZ(360deg) scale(.6);
}
75%{
    -webkit-transform: rotateZ(540deg) scale(.5);
            transform: rotateZ(540deg) scale(.5);
}
100%{
    -webkit-transform: rotateZ(720deg) scale(.4);
            transform: rotateZ(720deg) scale(.4);
}
}
@keyframes rotate-pill {
0%{
    -webkit-transform: rotateZ(0deg) scale(.8);
            transform: rotateZ(0deg) scale(.8);
}
25%{
    -webkit-transform: rotateZ(180deg) scale(.7);
            transform: rotateZ(180deg) scale(.7);
}
50%{
    -webkit-transform: rotateZ(360deg) scale(.6);
            transform: rotateZ(360deg) scale(.6);
}
75%{
    -webkit-transform: rotateZ(540deg) scale(.5);
            transform: rotateZ(540deg) scale(.5);
}
100%{
    -webkit-transform: rotateZ(720deg) scale(.4);
            transform: rotateZ(720deg) scale(.4);
}
}

